@tailwind base;
@tailwind components;
@tailwind utilities;

/* Fade-in animation for sections */
.fade-in {
	animation: fadeIn 1s ease-in;
}
@keyframes fadeIn {
	from { opacity: 0; transform: translateY(20px); }
	to { opacity: 1; transform: translateY(0); }
}

/* Button hover animation (subtle + accessible) */
.btn-animated {
	transition: transform 0.15s, box-shadow 0.15s, color 0.15s;
	will-change: transform;
}
.btn-animated:hover, .btn-animated:focus-visible {
	transform: scale(1.03);
	box-shadow: 0 6px 18px rgba(0,0,0,0.06);
}
.btn-animated:active {
	transform: scale(0.995);
}

/* Responsive adjustments */
@media (max-width: 768px) {
	.responsive-section {
		padding: 1rem;
		font-size: 1rem;
	}
}

/* Accessibility: only show focus outline for keyboard navigation */
:focus-visible {
	outline: 2px solid #2563eb;
	outline-offset: 2px;
}

/* Dashboard Button Styling */
.dashboard-button {
	/* Use variables for button visuals so dark mode can override them */
	background: var(--btn-gradient, linear-gradient(135deg, #667eea 0%, #764ba2 100%));
	color: var(--btn-text, white);
	padding: 0.75rem 1.5rem;
	border-radius: 12px;
	font-weight: 600;
	font-size: 1rem;
	border: none;
	cursor: pointer;
	transition: all 0.3s ease;
	box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
	white-space: nowrap;
}

/* Responsive button sizing */
@media (max-width: 768px) {
	.dashboard-button {
		padding: 0.5rem 1rem;
		font-size: 0.9rem;
	}
}

@media (max-width: 480px) {
	.dashboard-button {
		padding: 0.4rem 0.8rem;
		font-size: 0.8rem;
	}
}

.dashboard-button:hover {
	transform: translateY(-2px);
	box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.dashboard-button.contact {
	background: var(--btn-gradient-contact, linear-gradient(135deg, #a855f7 0%, #3b82f6 100%));
	box-shadow: 0 4px 15px rgba(168, 85, 247, 0.3);
}

.dashboard-button.contact:hover {
	box-shadow: 0 8px 25px rgba(168, 85, 247, 0.4);
}

.dashboard-button.documents {
	background: var(--btn-gradient-documents, linear-gradient(135deg, #10b981 0%, #059669 100%));
	box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3);
}

.dashboard-button.documents:hover {
	box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4);
}

.dashboard-button.password {
	background: var(--btn-gradient-password, linear-gradient(135deg, #f59e0b 0%, #d97706 100%));
	box-shadow: 0 4px 15px rgba(245, 158, 11, 0.3);
}

.dashboard-button.password:hover {
	box-shadow: 0 8px 25px rgba(245, 158, 11, 0.4);
}

.dashboard-button.logout {
	background: var(--btn-gradient-logout, linear-gradient(135deg, #ef4444 0%, #dc2626 100%));
	box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
}

.dashboard-button.logout:hover {
	box-shadow: 0 8px 25px rgba(239, 68, 68, 0.4);
}

/* Card styling improvements */
.dashboard-card {
	background: rgba(255, 255, 255, 0.95);
	backdrop-filter: blur(10px);
	border-radius: 20px;
	padding: 2rem;
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
	border: 1px solid rgba(255, 255, 255, 0.2);
	transition: all 0.3s ease;
}

.dashboard-card:hover {
	transform: translateY(-5px);
	box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

/* Form styling improvements */
.form-input {
	background: rgba(255, 255, 255, 0.9);
	border: 2px solid rgba(99, 102, 241, 0.2);
	border-radius: 12px;
	padding: 0.75rem 1rem;
	font-size: 1rem;
	transition: all 0.3s ease;
	width: 100%;
}

.form-input:focus {
	border-color: #6366f1;
	box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
	outline: none;
}

.form-textarea {
	background: rgba(255, 255, 255, 0.9);
	border: 2px solid rgba(99, 102, 241, 0.2);
	border-radius: 12px;
	padding: 0.75rem 1rem;
	font-size: 1rem;
	transition: all 0.3s ease;
	width: 100%;
	min-height: 120px;
	resize: vertical;
}

.form-textarea:focus {
	border-color: #6366f1;
	box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
	outline: none;
}

/* Message status badges */
.status-badge {
	padding: 0.25rem 0.75rem;
	border-radius: 20px;
	font-size: 0.85rem;
	font-weight: 600;
	text-transform: uppercase;
	letter-spacing: 0.5px;
}

.status-open {
	background: var(--status-open, linear-gradient(135deg, #3b82f6, #1d4ed8));
	color: var(--status-text, white);
}

.status-in_progress {
	background: var(--status-in_progress, linear-gradient(135deg, #f59e0b, #d97706));
	color: var(--status-text, white);
}

.status-resolved {
	background: var(--status-resolved, linear-gradient(135deg, #10b981, #059669));
	color: var(--status-text, white);
}

.status-closed {
	background: var(--status-closed, linear-gradient(135deg, #6b7280, #4b5563));
	color: var(--status-text, white);
}

/* Large, prominent status badges for admin views */
.status-badge-lg {
	padding: 0.35rem 0.9rem;
	border-radius: 9999px;
	font-size: 0.9rem;
}

/* Common app statuses */
.status-pending {
	background: linear-gradient(135deg, #f97316, #ea580c);
	color: white;
}

.status-pending_verification {
	background: linear-gradient(135deg, #7c3aed, #6d28d9);
	color: white;
}

.status-completed {
	background: linear-gradient(135deg, #10b981, #059669);
	color: white;
}

/* Table styling improvements */
.admin-table {
	background: var(--admin-table-bg, white);
	border-radius: 12px;
	overflow: hidden;
	box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.admin-table thead {
	background: var(--thead-gradient, linear-gradient(135deg, #6366f1, #4f46e5));
	color: var(--thead-text, white);
}

.admin-table thead th {
	padding: 1rem;
	font-weight: 600;
	text-align: left;
}

.admin-table tbody tr {
	border-bottom: 1px solid #f3f4f6;
	transition: background-color 0.2s ease;
}

.admin-table tbody tr:hover {
	background-color: #f8fafc;
}

.admin-table tbody td {
	padding: 1rem;
	vertical-align: top;
}

/* Modal improvements */
.modal-overlay {
	background: rgba(0, 0, 0, 0.6);
	backdrop-filter: blur(4px);
}

.modal-content {
	background: white;
	border-radius: 20px;
	box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

/* Button variants */
.btn-primary {
	background: linear-gradient(135deg, #6366f1, #4f46e5);
	color: white;
	padding: 0.75rem 1.5rem;
	border-radius: 10px;
	font-weight: 600;
	border: none;
	cursor: pointer;
	transition: all 0.3s ease;
	box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
}

.btn-primary:hover {
	transform: translateY(-2px);
	box-shadow: 0 8px 25px rgba(99, 102, 241, 0.4);
}

.btn-secondary {
	background: linear-gradient(135deg, #6b7280, #4b5563);
	color: white;
	padding: 0.75rem 1.5rem;
	border-radius: 10px;
	font-weight: 600;
	border: none;
	cursor: pointer;
	transition: all 0.3s ease;
	box-shadow: 0 4px 15px rgba(107, 114, 128, 0.3);
}

.btn-secondary:hover {
	transform: translateY(-2px);
	box-shadow: 0 8px 25px rgba(107, 114, 128, 0.4);
}

/* Personal info section styling */
.info-section {
	background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
	border-radius: 16px;
	padding: 2rem;
	margin-top: 2rem;
	border: 1px solid rgba(148, 163, 184, 0.2);
}

.info-grid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	gap: 1.5rem;
	margin-top: 1rem;
}

.info-field {
	background: white;
	padding: 1rem;
	border-radius: 12px;
	border: 1px solid rgba(203, 213, 225, 0.5);
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.info-label {
	font-weight: 600;
	color: #374151;
	font-size: 0.9rem;
	margin-bottom: 0.5rem;
	text-transform: uppercase;
	letter-spacing: 0.5px;
}

.info-value {
	color: #6b7280;
	font-size: 1rem;
	background: #f9fafb;
	padding: 0.5rem;
	border-radius: 8px;
	border: 1px solid #e5e7eb;
}

/* Dark mode variables and base rules (toggle by adding `dark` class to <html>) */
:root {
	--bg: #f8fafc;
	--surface: #ffffff;
	--text: #111827;
	--muted: #6b7280;
	--card-border: rgba(0, 0, 0, 0.05);

    /* Accent colors */
    --accent: #2E2A86;
    --accent-hover: #4b57b8;
}

.dark {
	--bg: #071025; /* deep navy */
	--surface: #0d1722; /* slightly lighter than bg for cards */
	--text: #e6eef8; /* soft off-white for text */
	--muted: #94a3b8; /* muted text */
	--card-border: rgba(255, 255, 255, 0.04);

	/* Dark variants for previously hard-coded visuals */
	--btn-gradient: linear-gradient(135deg, #2b3250 0%, #071026 100%);
	--btn-text: #e6eef8;
	--btn-gradient-contact: linear-gradient(135deg, #4b2f9e 0%, #11306a 100%);
	--btn-gradient-documents: linear-gradient(135deg, #0c6b4a 0%, #063e2a 100%);
	--btn-gradient-password: linear-gradient(135deg, #7c4a00 0%, #3f2500 100%);
	--btn-gradient-logout: linear-gradient(135deg, #6b1f25 0%, #2f0f10 100%);

	--status-open: linear-gradient(135deg, #2563eb, #1e3a8a);
	--status-in_progress: linear-gradient(135deg, #b45309, #92400e);
	--status-resolved: linear-gradient(135deg, #0f9d58, #0a6b42);
	--status-closed: linear-gradient(135deg, #4b5563, #2d3748);
	--status-text: #eef2ff;

	--admin-table-bg: var(--surface);
	--thead-gradient: linear-gradient(135deg, #0b1220 0%, #071028 100%);
	--thead-text: var(--text);
}

/* Utility overrides to improve dark-mode readability for elements that use Tailwind utility classes or hard-coded colors */
.dark .bg-white,
.dark .bg-gray-50,
.dark .bg-gray-100,
.dark .bg-gray-200,
.dark .bg-gray-300 {
	background-color: var(--surface) !important;
	color: var(--text) !important;
}

.dark .text-gray-700,
.dark .text-gray-800,
.dark .text-gray-900,
.dark .text-black,
.dark .text-white {
	color: var(--text) !important;
}

.dark .border-gray-200,
.dark .border-gray-100,
.dark .border-gray-300 {
	border-color: var(--card-border) !important;
}

/* Links and accents in dark mode */
.dark a:not(.dashboard-button) {
	color: var(--accent-hover) !important;
}
.dark a:not(.dashboard-button):hover {
	color: var(--accent) !important;
}

/* Make hero-like large sections slightly darker overlay for readability (if present) */
.dark .hero,
.dark .page-hero,
.dark .hero-banner {
	position: relative;
}
.dark .hero::before,
.dark .page-hero::before,
.dark .hero-banner::before {
	content: '';
	position: absolute;
	inset: 0;
	background: linear-gradient(rgba(2,6,23,0.55), rgba(2,6,23,0.55));
	pointer-events: none;
	z-index: 0;
}
.dark .hero *,
.dark .page-hero *,
.dark .hero-banner * {
	position: relative;
	z-index: 1;
}

/* Slightly soften gradients in dark mode for better contrast */
.dark .dashboard-button,
.dark .dashboard-button.contact,
.dark .dashboard-button.documents,
.dark .dashboard-button.password,
.dark .dashboard-button.logout {
	box-shadow: 0 6px 20px rgba(0,0,0,0.45) !important;
	border: 1px solid rgba(255,255,255,0.03);
}

/* Improve table header contrast */
.dark .admin-table thead th {
	background: linear-gradient(135deg, rgba(7,16,40,0.95), rgba(11,18,32,0.95));
}

/* Card surface tweaks in dark mode */
.dark .dashboard-card,
.dark .modal-content,
.dark .info-section {
	background-color: color-mix(in srgb, var(--surface) 85%, black 15%) !important;
	box-shadow: 0 8px 30px rgba(2,6,23,0.6);
	border-color: rgba(255,255,255,0.03) !important;
}

/* Improve small text contrast */
.dark .info-value,
.dark .info-label,
.dark .status-badge {
	color: var(--muted) !important;
}

/* Buttons: subtle hover lift */
.dark .dashboard-button:hover {
	transform: translateY(-3px);
	box-shadow: 0 12px 30px rgba(2,6,23,0.6) !important;
}

/* Hero CTA (distinct from dashboard buttons) */
.hero-cta {
	background: var(--accent);
	color: white;
	font-weight: 700;
	box-shadow: 0 8px 30px rgba(46,42,134,0.15);
	border: 1px solid rgba(0,0,0,0.06);
}
.hero-cta:hover {
	background: var(--accent-hover);
}

/* Dark mode variant for hero CTA */
.dark .hero-cta {
	background: #3b2f66; /* slightly desaturated purple for contrast */
	color: #f7f9ff;
	box-shadow: 0 8px 30px rgba(2,6,23,0.5);
	border: 1px solid rgba(255,255,255,0.04);
}

/* Subtle highlights around containers to improve readability */
/* container-highlight removed per user request (highlights disabled) */

/* Login/background gradient overrides: make light hero gradients dark in `.dark` */
.dark .bg-gradient-to-br,
.dark .bg-gradient-to-tl,
.dark .bg-gradient-to-tr,
.dark .bg-gradient-to-bl,
.dark [class*="from-blue-"],
.dark [class*="to-blue-"] {
	background: linear-gradient(135deg, rgba(7,16,25,0.9), rgba(7,16,25,0.85)) !important;
}

/* Inputs and form elements: ensure surface + readable text in dark mode */
.dark input,
.dark textarea,
.dark select,
.dark .form-input,
.dark .form-textarea {
	background-color: var(--surface) !important;
	color: var(--text) !important;
	border-color: rgba(255,255,255,0.06) !important;
}

.dark input::placeholder,
.dark textarea::placeholder {
	color: rgba(230,238,248,0.6) !important;
}

/* Override common Tailwind blue shades used for buttons/links on login */
.dark .bg-blue-700 { background-color: var(--accent) !important; }
.dark .hover\:bg-blue-800:hover { background-color: var(--accent-hover) !important; }
.dark .text-blue-700 { color: var(--accent) !important; }
.dark .text-blue-800:hover { color: var(--accent-hover) !important; }

html, body {
	background-color: var(--bg);
	color: var(--text);
	transition: background-color 200ms ease, color 200ms ease;
}

/* Make common components follow the color variables */
.dashboard-card,
.admin-table,
.modal-content,
.info-section,
.form-input,
.form-textarea {
	background-color: var(--surface) !important;
	color: var(--text) !important;
	border-color: var(--card-border) !important;
}

/* Muted text */
.info-value,
.info-label,
.status-badge {
	color: var(--muted) !important;
}

/* Smooth theme transition for interactive elements */
a, button, input, textarea {
	transition: background-color 200ms ease, color 200ms ease, border-color 200ms ease;
}

/* Theme-aware overrides for common Tailwind utilities
   These make existing markup that uses `bg-white`, `bg-gray-50`, etc.
   follow the theme variables so dark mode applies without changing JSX. */
.bg-white, .bg-gray-50, .bg-gray-100 {
	background-color: var(--surface) !important;
}

/* Make common light borders follow the theme */
.border-gray-200, .border-gray-100 {
	border-color: var(--card-border) !important;
}

/* Ensure table hover/background interactions respect the theme */
.admin-table tbody tr:hover {
	background-color: rgba(255,255,255,0.03); /* subtle in dark, near-white in light */
}

/* Small utility so sections using explicit white background collapse to surface */
.section-white {
	background-color: var(--surface) !important;
}

